import React, { memo } from 'react'
import { createStyles, Text } from '@instacart/cocktail'
import PropTypes from 'prop-types'

const useStyles = createStyles(
  tokens => ({
    root: {
      width: '100%',
      backgroundColor: tokens.color.neutral.grey1,
      padding: tokens.size.spacing.xxSmall,
    },
    header: {
      fontSize: tokens.size.font.body,
      color: tokens.color.text.primary,
    },
    messageBox: {
      fontSize: tokens.size.font.caption,
      color: tokens.color.text.secondary,
      WebkitTextSizeAdjust: '100%',
    },
  }),
  { displayName: 'CartRewardsTile' }
)

const CartRewardsTile = props => {
  const styles = useStyles(props)
  const { titleText, value, content } = props
  const separator = ':'

  return (
    <div css={styles.root}>
      <Text css={styles.header}>
        {titleText}
        {separator} {value}
      </Text>
      <Text
        css={styles.messageBox}
        // eslint-disable-next-line react/no-danger
        dangerouslySetInnerHTML={{ __html: content }}
      />
    </div>
  )
}

CartRewardsTile.propTypes = {
  /** Balance of the rewards */
  value: PropTypes.string.isRequired,
  /** Title of the rewards */
  titleText: PropTypes.string.isRequired,
  /** Message to display */
  content: PropTypes.string.isRequired,
}

export default memo(CartRewardsTile)
